/**
 * @module widget-fx-rate-table-details-ng
 *
 * @description
 * FX rate table details widget.
 *
 *
 * @example
 *  <div ng-controller="FXRateTableDetailsController as $ctrl">
 *    <ul>
 *       <li data-ng-repeat="item in ext.helpers.currencyPairs()">{{item.pairKey}}</li>
 *    </ul>
 *  </div>
 */
import angular from 'vendor-bb-angular';
import extendHooks from 'lib-bb-widget-extension-ng';
import stateContainerModuleKey, { bbStateContainerKey } from 'lib-bb-state-container-ng';
import widgetModuleKey, { widgetKey } from 'lib-bb-widget-ng';
import modelFxTradingModuleKey, { modelFxTradingKey } from 'model-fx-trading-ng';
import bbIntentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';
import eventBusModuleKey, { eventBusKey } from 'lib-bb-event-bus-ng';
import Controller from './controller';
import defaultHooks from './default-hooks';
import service from './service';
import viewModel from './view-model';

const hooksKey = 'widget-fx-rate-table-details-ng:hooks';
const serviceKey = 'widget-fx-rate-table-details-ng:service';
const viewModelKey = 'widget-fx-rate-table-details-ng:viewModel';

/**
 * @name default
 * @type {string}
 * @description Angular module name
 */
export default angular
  .module('widget-fx-rate-table-details-ng', [
    modelFxTradingModuleKey,
    widgetModuleKey,
    bbIntentModuleKey,
    eventBusModuleKey,
    stateContainerModuleKey,
  ])

  .factory(hooksKey, extendHooks(defaultHooks))
  .factory(serviceKey, [
    modelFxTradingKey,
    viewModelKey,
    service,
  ])
  .factory(viewModelKey, [
    bbStateContainerKey,
    widgetKey,
    hooksKey,
    viewModel,
  ])


  .controller('FXRateTableDetailsController', [
    widgetKey,
    bbIntentKey,
    serviceKey,
    viewModelKey,
    eventBusKey,
    /* into */
    Controller,
  ])
  .name;

/**
 * CurrencyPair type definition
 * @typedef {Object} CurrencyPair
 * @property {string}        pairKey          - Currency pair id
 */

 /**
 * CurrencyRate type definition
 * @typedef {Object} CurrencyRate
 * @property {string}        pairKey          - Currency pair id
 * @property {number}        price            - Last executed price for the currency pair
 * @property {number}        change           - Today's price change
 * @property {number}        changePct        - Today's price change in percent
 * @property {number}        bid              - Bid price available for the currency pair
 * @property {number}        ask              - Ask price available for the currency pair
 * @property {number}        ytdReturn        - Percentage of currency pair price change
 * @property {number}        wkRangeFrom      - A range from of price change for 52 recent weeks
 * @property {number}        wkRangeTo        - A range to of price change for 52 recent weeks
 * @property {number}        dayRangeFrom     - Today's range to of price change
 * @property {number}        dayRangeTo       - Today's range to of price change
 */



