/* global angular */
export default (model, viewModel) => {
  let timerId;
  /**
   * @description
   * Handles loading rate details data of currency pair
   * @public
   * @name loadCurrencyRates
   * @type {function}
   * Load currency rate details
   * @returns {promise.<array>}
   */
  const loadCurrencyRates = () => {
    const currentParams = {
      currencypair: [viewModel.getState().currencyPair.data.pairKey],
    };

    return model.loadCurrencyRates(currentParams)
      .then((data) => {
        viewModel.afterLoadCurrencyRatesSuccess({
          data: data.data,
        });
        return data;
      })
      .catch((error) => {
        viewModel.afterLoadCurrencyRatesError(error);
        throw error;
      });
  };

  /**
   * @description
   * Method for stop rates auto update by interval
   * @public
   * @name stopRatesUpdate
   * @type {function}
   * Stop rates updates
   */
  const stopRatesUpdate = () => {
    if (timerId) {
      clearInterval(timerId);
      timerId = null;
    }
  };

  /**
   * @description
   * Handles loading currency details data
   * @public
   * @name load
   * @param {number} intervalUpdateRates interval of currency details update
   * @type {function}
   * Load currency details
   * @returns {promise.<array>}
   */
  const load = (intervalUpdateRates) => {
    viewModel.beforeLoadCurrencyPair();
    stopRatesUpdate();
    return model.getSelectedCurrencyPairId()
      .then(currencyPairId => model.loadCurrencyPairDetails(currencyPairId))
      .then((data) => {
        viewModel.afterLoadCurrencyPairSuccess({
          data: data.data,
        });
        return data;
      })
      .then(() => {
        loadCurrencyRates();
      })
      .then(() => {
        timerId = setInterval(() => { loadCurrencyRates(); }, intervalUpdateRates);
      })
      .catch((error) => {
        viewModel.afterLoadCurrencyPairError(error);
        throw error;
      });
  };

  /**
   * @name setCurrencyPairIdSelected
   * @type {function}
   * @description
   * Set current selected currency pair id
   * @param {string} currencyPairId currency pair id to set selected
   */
  const setCurrencyPairIdSelected = (currencyPairId) => {
    model.setCurrencyPairIdSelected(currencyPairId);
  };

  return {
    load,
    loadCurrencyRates,
    stopRatesUpdate,
    setCurrencyPairIdSelected,
  };
};



