/* global angular*/
import { Preference, Event } from './constants';

/**
 * @name FXRateTableDetailsController
 * @ngkey FXRateTableDetailsController
 * @type {object}
 * @description
 * FX rate table details controller.
 */
export default function FXRateTableDetailsController(
  widgetInstance,
  bbIntents,
  service,
  viewModel,
  eventBus
) {
  const $ctrl = this;

  /**
   * @name intervalUpdateRates
   * @type {Number} intervalUpdateRates
   * @description
   * Delay of update table rate details
   */
  const intervalUpdateRates =
    widgetInstance.getLongPreference(Preference.INTERVAL_UPDATE_RATES) * 1000;

  /**
   * @description
   * Handles loading trading rate table details data
   *
   * @public
   * @name FXRateTableDetailsController#load
   * @type {function}
   * Load rate table details data
   * @returns {promise.<array>}
   */
  const load = () =>
    service.load(intervalUpdateRates);

  /*
   * @description
   * Widget initialization logic.
   * Loads rate table details data
   */
  const $onInit = () => {
    viewModel.init();
    return load();
  };

  /**
   * @name viewTradeOrder
   *
   * @description
   * view trade order modal
   *
   * @param {string} currencyPairId currency pair id
   * @type {function}
   */
  const viewTradeOrder = (currencyPairId) => {
    service.setCurrencyPairIdSelected(currencyPairId);
    eventBus.publish(Event.SHOW_TRADE_ORDER);
  };

  const $onDestroy = () => service.stopRatesUpdate();

  Object.assign($ctrl, {
    $onInit,
    $onDestroy,
    viewTradeOrder,
    load,
  });

  return Object.assign(
    {
      get state() {
        return viewModel.getState();
      },
      get errors() {
        return viewModel.getState().errors;
      },
    },
    $ctrl
  );
}



