import { wrapActions } from 'lib-fx-trading-view-model-helper';

export default function (
  stateContainer
) {
  /**
   * @name initialState
   * @description Initial state of rate table store
   * @type {object}
   * @property {object}  currencyPairs      An object of currency pairs
   * @property {boolean} initialLoading     Boolean flag of initial loading
   * @property {object}  errors             currency pairs errors
   */
  const initialState = {
    currencyPair: {
      data: {},
      loading: false,
    },
    currencyRates: {
      data: {},
    },
    initialLoading: true,
    timestamp: null,
    errors: {
      currencyPairError: null,
      currencyRatesError: null,
    },
  };

  return {
    getState: () => stateContainer.getState(),
    ...wrapActions(stateContainer, {
      init: (state = initialState) => state,
      beforeLoadCurrencyPair: () => ({
        currencyPair: {
          loading: true,
        },
      }),
      afterLoadCurrencyPairSuccess: (state, raw) => ({
        initialLoading: false,
        currencyPair: {
          loading: false,
          data: raw.data,
        },
        timestamp: Date.now(),
        errors: {
          currencyPairError: null,
        },
      }),
      afterLoadCurrencyPairError: (state, error) => ({
        initialLoading: false,
        currencyPair: {
          loading: false,
          data: {},
        },
        errors: {
          currencyPairError: error.code,
        },
      }),
      afterLoadCurrencyRatesSuccess: (state, raw) => ({
        initialLoading: false,
        currencyRates: {
          data: raw.data[0],
        },
        errors: {
          currencyRatesError: null,
        },
      }),
      afterLoadCurrencyRatesError: (state, error) => ({
        initialLoading: false,
        currencyRates: {
          data: {},
        },
        errors: {
          currencyRatesError: error.code,
        },
      }),
    }),
  };
}



